@import 'vars';

.wp-block {
  max-width: $section;
  &:not(:first-child) {
    margin-top: 70px;
  }
  &:not(:last-child) {
    margin-bottom: 70px;
  }
}

.editor-block-list__block-edit {
  font-size: $font-size;
  color: $body-colour;
  line-height: 1.6;
  font-family: $font-family-base;
  font-weight: $font-weight-base;
  background: $body-bg;
}

.editor-post-title__block .editor-post-title__input {
  font-family: $heading-font;
}

.wp-block-heading,
.wp-block {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-top: 0;
    font-family: $heading-font;
    color: $heading-colour;
  }
}

.flexible-content-container {
  &:not(:first-child) {
    margin-top: 70px;
  }
  &:not(:last-child) {
    margin-bottom: 70px;
  }
  img {
    display: block;
    height: auto;
    max-width: 100%;
  }

  .one-column,
  .two-columns,
  .three-columns {
    .columns {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
  .two-columns {
    .columns {
      .column {
        width: calc(50% - 45px);
        @media screen and (max-width: 992px) {
          width: 100%;
          &:not(:last-child) {
            margin-bottom: 60px;
          }
        }
      }
    }
  }
  .three-columns {
    .columns {
      .column {
        width: calc(33.3333% - 45px);
        @media screen and (max-width: 992px) {
          width: 100%;
          &:not(:last-child) {
            margin-bottom: 60px;
          }
        }
      }
    }
  }

  .two-images,
  .three-images {
    display: flex;
    justify-content: space-between;
  }

  .two-images {
    .image {
      width: calc(50% - 5px);
    }
  }

  .three-images {
    .image {
      width: calc(33.3333% - 5px);
    }
  }

  .image-and-content {
    .columns {
      display: flex;
      justify-content: space-between;
      .column {
        flex: 1;
        &:first-child {
          margin-right: 90px;
        }
      }
    }
    @media screen and (max-width: 992px) {
      .columns {
        .column {
          &:first-child {
            margin-right: 40px;
          }
        }
      }
    }
    @media screen and (max-width: 768px) {
      &.image-right {
        .columns {
          .column {
            &:first-child {
              margin-right: 0;
              margin-bottom: 40px;
            }
          }
        }
      }
      &.image-left {
        .columns {
          flex-direction: column-reverse;
          .column {
            &:last-child {
              margin-right: 0;
              margin-bottom: 40px;
            }
          }
        }
      }
      .columns {
        flex-wrap: wrap;
        .column {
          flex: none;
          width: 100%;
        }
      }
      .image {
        img {
          margin: 0 auto;
        }
      }
    }
  }

  .highlighted-section {
    padding: 30px 40px;
    background: $grey-200;
    @media screen and (max-width: 576px) {
      padding: 22px 25px;
    }
  }

  .youtube-video {
    max-width: 800px;
    margin: 0 auto;
    .wrapper {
      position: relative;
      padding-top: 56.25%;
    }
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
  }
}